.app {
  box-sizing: border-box;
  position: relative;
  text-align: center;
  max-width: 600px;
  margin: 0 auto;
  background-color: #ccc;
}

.forecast {
  box-sizing: border-box;
  color: white;

  &__day {
    font-size: 32px;
    font-weight: bold;
    padding-left: 30px;

    @media (max-width: 600px) {
      font-size: 24px;
      padding-left: 24px;
    }

    @media (max-width: 400px) {
      font-size: 16px;
      padding-left: 16px;
    }
  }

  &__weather {
    // border: 1px solid green;
  }
  &__temp {
    // border: 1px solid green;
  }
  &__wind {
    // border: 1px solid green;
  }
}

.fact {
  box-sizing: border-box;
  border: 1px solid green;
  max-height: 375px;
  height: 61vw;
}

.forecast__row {
  box-sizing: border-box;
  display: grid;
  grid-template-columns: 4.5fr 1.5fr 2fr 2fr 2fr;
  border: 1px solid blue;
  max-height: 94px;
  height: 15.6vw;
  cursor: pointer;
}

.forecast__row-full {
  box-sizing: border-box;
  border: 1px solid blue;
  max-height: 370px;
  height: 61vw;
  cursor: pointer;
  background-color: red;
}
/* forecast__row */
.row-enter {
  height: 0;
  opacity: 0;
}

.row-enter-active {
  height: 94px;
  opacity: 1;
  transition: 300ms linear;

  @media (max-width: 600px) {
    height: 15.6vw;
  }
}

.row-exit {
  height: 94px;
  opacity: 1;
  @media (max-width: 600px) {
    height: 15.6vw;
  }
}

.row-exit-active {
  height: 0;
  opacity: 0;
  transition: 300ms linear;
}
/* forecast__row-full */
.row-full-enter {
  height: 0;
  opacity: 0;
}

.row-full-enter-active {
  opacity: 1;
  height: 370px;
  transition: 300ms linear;

  @media (max-width: 600px) {
    height: 61vw;
  }
}

.row-full-exit {
  opacity: 1;
  height: 370px;
  @media (max-width: 600px) {
    height: 61vw;
  }
}

.row-full-exit-active {
  height: 0;
  opacity: 0;
  transition: 300ms linear;
}

.row__item {
  display: flex;
  // justify-content: center;
  align-items: center;
}